import React from 'react';
import Rooms_filter from './rooms_filter'
import Rooms_list from './rooms_list'
import {with_room_consumer} from '../context'
import Loading from './loading'

function RoomContainer({context}){
	const {loading, sorted_rooms, rooms} = context
	if (loading) {
		return <Loading /> ;
		}
	return (
		<div>
			<Rooms_filter rooms={rooms}/>
			<Rooms_list rooms={sorted_rooms}/>
	    </div>
			)
}




export default with_room_consumer(RoomContainer)

