import React, { Component } from 'react';
import items from './data'; 
import Client from './Contentful'
const RoomContext = React.createContext();

Client.getEntries({
	content_type: "resortProject"
})
.then((response) => console.log(response.items))
.catch(console.error)


class RoomProvider extends Component {
state = {
	rooms: [],
	sorted_rooms: [],
	featured_rooms: [], 
	loading: true, 
	type: 'all', 
	capacity: 1 , 
	price: 0 ,
	max_price: 0 ,
	min_price: 0 , 
	min_size: 0,
	max_size: 0 ,
	breakfast: false,
	pets: false,   

};
// get data for 
get_data = async () =>{
	try{
		let response = await Client.getEntries({
			content_type : "resortProject", 
			order:'fields.price'
		});
		let rooms = this.formatData(items)
		let featured_rooms = rooms.filter(room => room.featured === true)
		let max_price = Math.max(...rooms.map(item => item.price));
		let max_size = Math.max(...rooms.map(item => item.size));
		this.setState({
			rooms,
			featured_rooms,
			sorted_rooms: rooms,
			loading: false, 
			price: max_price, 
			max_price, 
			max_size
		});

	}
	catch(error){
		console.log(error)

	}
} 

componentDidMount(){
	this.get_data()
	}

formatData(items){
	let tempItems = items.map(item=> {
		let id = item.sys.id;
		let images = item.fields.images.map(image=> image.fields.file.url);
		let rooms = {...item.fields, images, id}; 
		return rooms
	}); 
	return tempItems

}
get_room = (slug) => {
	let temp_rooms = [...this.state.rooms];
	const rooms = temp_rooms.find((room) => room.slug === slug)// find returns a obj and returns the first match 
	return rooms; 
}

handle_change = event => {
	const target = event.target
	const value= target.type === 'checkbox'? 
	target.checked : target.value
	const name = event.target.name
	this.setState({
		[name]: value
	}, this.filter_rooms);
}


filter_rooms = () => {
	let{rooms, type, capacity,price, min_size, max_size, breakfast, pets} = this.state
	// all the rooms 
	let temp_rooms = [...rooms]; 
	// transform values
	capacity = parseInt(capacity)
	price = parseInt(price)

	//filter by type
	if (type!== 'all'){
		temp_rooms = temp_rooms.filter(room => room.type === type)
	}
	// filter by capacity  
	if (capacity !==1){
		temp_rooms = temp_rooms.filter(room => room.capacity >= capacity);
	}
	// filter by price
	temp_rooms = temp_rooms.filter(room => room.price <= price);

	// filter by size 
	temp_rooms = temp_rooms.filter(room => room.size >=min_size && room.size <= max_size)

	//filter by breakfast 
	if (breakfast){
		temp_rooms = temp_rooms.filter(room => room.breakfast === true)
	}
	// filter by pets
	if (pets){
		temp_rooms = temp_rooms.filter(room => room.pets === true)
	}



	//change state
	this.setState({
		sorted_rooms : temp_rooms
	});
}
	render() {
		return (
			<RoomContext.Provider value={{ ...this.state, get_room: this.get_room, handle_change: this.handle_change }}>
				{this.props.children}
			</RoomContext.Provider>
		);
	}
}

const RoomConsumer = RoomContext.Consumer; 



export function with_room_consumer(Component){
	return function consumer_wrapper(props){
		return <RoomConsumer>
		{value => <Component {...props} context={value}/>}
		</RoomConsumer> 
	}
}
export { RoomProvider, RoomConsumer, RoomContext } ; 