 import React from 'react';
 import {useContext} from 'react'
 import {RoomContext} from '../context'
 import Title from "../components/Title"
// get all unique values

const get_unique = (items, value) => {
	return [... new Set(items.map(item => item[value]))]
}
export default function Rooms_filter({rooms}){
  	const context = useContext(RoomContext);
  	const {
  		handle_change, 
  		type, 
  		capacity,
  		price,
  		min_price,
  		max_price, 
  		min_size,
  		max_size, 
  		breakfast,
  		pets} = context;

	// get unique types
	let types = get_unique(rooms, 'type'); 
	// add all 
	types = ['all', ...types]
	// map to jsx
	types = types.map((item, index) => {
		return <option value={item} key={index}>{item}</option> 
	})
	let people = get_unique(rooms, 'capacity');
	people = people.map((item, index) => {
		return <option key={index} value={item}>{item}</option> 
	})
   return (
   	<section className="filter-container">
		<Title title="search room" />
		<form className="filter-form">
		{/* select type*/}
		<div className="form-group">
			<label htmlFor="type"> room type </label>
			<select name="type"
			 		id="type"
			 		value={type} 
			 		className="form-control" 
			 		onChange={handle_change}>
					{types}
			 		</select>
		</div>
		{/* endselect type*/}
	{/* guests */}
		<div className="form-group">
			<label htmlFor="capacity">Guests </label>
			<select name="capacity"
			 		id="capacity"
			 		value={capacity} 
			 		className="form-control" 
			 		onChange={handle_change}>
					{people}
			 		</select>
		</div>
		{/* guests */}
		{/* room price */}
		<div className="form-group">
			<label htmlFor="price">room price ${price}</label>
			<input type="range" name="price" min={min_price}
			max={max_price} id="price" value={price} onChange={handle_change} className="form-control" />

		</div>
		{/* end room price  */}
		{/* Size  */}
			<div className="form-group">
			<label htmlFor="size">Room size</label>
			<div className="size-inputs">
				<input type="number" name="min_size" id="size"
				value={min_size} onChange={handle_change} className="size-input"/>

				<input type="number" name="max_size" id="size"
				value={max_size} onChange={handle_change} className="size-input"/>
			</div>
			</div>
		{/* end size  */}
		{/* extras  */}
			<div className="form-group">
			<div className="single-extra">
			<input type="checkbox"
					name="breakfast" 
					id="breakfast"
					checked={breakfast}
					onChange={handle_change} />
			<label htmlFor="breakfast">breakfast</label>
			</div>

			<div className="single-extra">
			<input type="checkbox"
					name="pets" 
					id="pets"
					checked={pets}
					onChange={handle_change} />
			<label htmlFor="pets">pets</label>
			</div>
			</div>


		{/* end extras  */}



	
		</form>
   	</section>
   )
 }
 
